	module.exports = {"data":{"wordpressPost":{"title":"Ultra-processed foods, drinks marketed to children, and cannabis use in psychosis: Most read articles in March","content":"<h1>Ultra-processed foods and added sugars, the content of drinks marketed to children, and cannabis use in first episode psychosis</h1>\n<p><img class=\"alignnone\" title=\"Image: Unhealthy snacks in cart.jpg, from Wikimedia Commons, by Unknown photographer\" src=\"https://upload.wikimedia.org/wikipedia/commons/thumb/8/8e/Unhealthy_snacks_in_cart.jpg/800px-Unhealthy_snacks_in_cart.jpg\" alt=\"File:Unhealthy snacks in cart.jpg\" width=\"363\" height=\"242\" /></p>\n<p>March was a busy month here at <em>BMJ Open</em>, with many new papers entering the Top 10 Most Read. In at number one this month is a popular paper which was press released and saw a lot of activity on Twitter: a cross-sectional study investigating the contribution of ultra-processed foods to the intake of added sugars in the USA. Boulton <em>et al</em> enter the list at number two with a much discussed survey of fruit juices to determine the sugar content of drinks marketed to children, which made national news, and Brown <em>et al</em> review the effectiveness of community pharmacy-delivered interventions for alcohol reduction, smoking cessation and weight management at number five. Investigating the association of cannabis use with hospital admission and antipsychotic treatment failure in patients with first episode psychosis, Patel <em>et al</em> come in at number five. Finally, at number nine we have an interesting analysis of systematic reviews by Elia et al which asks how authors of systematic reviews deal with research malpractice and misconduct in original studies.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Martinez Steele<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/3/e009892.full\">Ultra-processed foods and added sugars in the US diet: evidence from a nationally representative cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Boulton <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/3/e010330.full\">How much sugar is hidden in drinks marketed to children? A survey of fruit juices, juice drinks and smoothies</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Griffiths <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e008751.full\">Registered nurse, healthcare support worker, medical staffing levels and mortality in English hospital trusts: a cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Kristensen<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e007118.full\">The effect of statins on average survival in randomised trials, an analysis of end point postponement</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Brown <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e009828.full\">Community pharmacy-delivered interventions for public health priorities: a systematic review of interventions for alcohol reduction, smoking cessation and weight management, including meta-analysis for smoking cessation</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Patel <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/3/e009888.full\">Association of cannabis use with hospital admission and antipsychotic treatment failure in first episode psychosis: an observational study</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Shyangdan <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e009417.full\">SGLT-2 receptor inhibitors for treating patients with type 2 diabetes mellitus: a systematic review and network meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Gattrell<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e010329.full\">Professional medical writing support and the quality of randomised controlled trial reporting: a cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Elia<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/3/e010442.full\">How do authors of systematic reviews deal with research malpractice and misconduct in original studies? A cross-sectional analysis of systematic reviews and survey of their authors</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Mekonnen <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e010003.full\">Effectiveness of pharmacist-led medication reconciliation programmes on clinical outcomes at hospital transitions: a systematic review and meta-analysis</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"3fcc6966-714b-515b-88fc-118e1d84cfd8"}}

